    .config-form {
        display: flex;
        flex-direction: column;
        gap: 15px; 
        font-size: 14px;
    }
    
    label {
        display: flex;
        flex-direction: column;
        font-weight: bold;
    }
    
    .color-input {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
    
    .input-wrapper {
        display: flex;
        align-items: center;
        gap: 10px; 
    }
    
    .input-wrapper input[type="text"] {
        flex-grow: 1;
        padding: 5px;
        border: 1px solid #ccc;
        border-radius: 4px;
        font-size: 14px;
    }
    
    .input-wrapper input[type="text"]:focus {
        border-color: #007bff;
        outline: none;
    }
    
    .input-wrapper .color-picker {
        width: 40px;
        height: 40px;
        border: none;
        cursor: pointer;
        border-radius: 4px;
    }
    
    input[type="text"],
    select {
        padding: 5px;
        border: 1px solid #ccc;
        border-radius: 4px;
        font-size: 14px;
    }
    
    input[type="text"]:focus,
    select:focus {
        border-color: #007bff;
        outline: none;
    }
    
    textarea {
        resize: vertical;
        padding: 5px;
        font-size: 14px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }
    
    textarea:focus {
        border-color: #007bff;
        outline: none;
    }
        