.app {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0px;
  gap: 20px;
  box-sizing: border-box;
  min-height: 100vh; 
}


.left-section {
  flex: 1;
  max-width: 48%;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  box-sizing: border-box;
}

.left-section h2 {
  margin-bottom: 20px;
  font-size: 18px;
  color: #333;
}


.left-section .launcher-image {
  width: 100%;
  height: 200px; 
  object-fit: cover;
  border-radius: 8px;
}


.right-section {
  flex: 1 1;
  background-color: #e2b996;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 120px;
  width: 500px;
  height: 750px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.live-preview {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #333;
}

.dot {
  width: 10px;
  height: 10px;
  background-color: green;
  border-radius: 50%;
  margin-right: 10px;
  position: relative;
  top: -2px; 
}




.welcome-text {
  font-size: 16px;
  color: #555;
  margin-top: 20px;
  text-align: center;
  width: 100%;
  padding: 10px 0;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-sizing: border-box;
  text-align: center;
}


.chat-widget {
  flex-grow: 1; 
  width: 100%;
  max-height: 60vh; 
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  background-color: #f4f4f4;
}


.download-button {
  width: 100%;
  padding: 10px 15px;
  margin-top: 20px;
  background-color: #28a745;
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.download-button:hover {
  background-color: #218838;
}




.load-config {
  margin-top: 20px;
}

.file-upload-label {
  display: inline-block;
  padding: 10px 15px;
  background-color: #0a0a0a;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
}
.left-section .load-config {
  margin-bottom: 20px; 
  text-align: left;
}

.file-upload-label:hover {
  background-color: #0056b3;
}


@media (max-width: 768px) {
  .app {
    flex-direction: column; 
  }

  .left-section,
  .right-section {
    width: 100%; 
    max-width: none;
  }

  .chat-widget {
    max-height: 50vh; 
  }
}
