.chat-widget-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.chat-widget-title {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333;
}

.chat-widget {
  width: 300px;
  max-height: 70vh; 
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.chat-header {
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #f1f1f1;
}

.avatar {
  width: 30px;
  height: 25px;
  border-radius: 50%;
  object-fit: cover;
}


.chat-body {
  padding: 10px;
  flex-grow: 1;
  overflow-y: auto;
}

.chat-footer {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #f9f9f9;
}

.chat-input {
  flex-grow: 1;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.chat-input:focus {
  outline: none;
  border-color: #007bff;
}

.mic-button {
  padding: 8px 12px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.mic-button:hover {
  background-color: #0056b3;
}
